                <el-menu
                        :default-active="defaultActive"
                        mode="horizontal"
                        background-color="#545c64"
                        text-color="#fff"
                        active-text-color="#ffd04b"
                        router>
                    <el-menu-item index="/bd/home" router>首页</el-menu-item>
                    <el-menu-item index="/bd/statistics" v-if="$store.state.user.role==0">监控</el-menu-item>
                    <el-menu-item index="/bd/list">业务</el-menu-item>
<!--                    <el-menu-item index="/bd/status">系统状态</el-menu-item>-->
