                    <el-dropdown-menu slot="dropdown" style="text-align: center">
                        <span style="font-size: 20px;padding: 6px 8px;">{{$store.state.user.account}}</span>
                        <el-dropdown-item divided command="logout">退出系统</el-dropdown-item>
                    </el-dropdown-menu>
