<script>
export default {
    name: "Layout",
    data: function () {
        return {
            defaultActive: 'home'
        }
    },
    watch: {
        '$route.path': function () {
            this.$store.dispatch('getprofile');
        }
    },
    beforeMount() {
        this.$store.dispatch('getprofile');
    },
    mounted() {
        this.defaultActive = this.$route.meta;
    },
    methods: {
        select: function () {
            this.$store.dispatch('logout');
            this.$router.replace('/');
        }
    }
}
</script>

